<template>
    <div class="page chats">   

        <topheader class="topheader">
            <template v-slot:left>
                <backButton action="chats"/>
            </template>
            <template v-slot:info>
                <span class="nameline">Bastyon</span>
            </template>
        </topheader>

        <maincontent>

            <template v-slot:content>

                <div id="messagesWrapper">

                    <div v-for="message in messages" :key="message" class="message">

                        <div v-for="phrase in message" :key="phrase">

                            <h1 v-if="phrase.type == 'title'">
                                {{ phrase.value }}
                            </h1>

                            <span v-if="phrase.type == 'message'">
                                {{ phrase.value }}
                            </span>

                            <ul v-if="phrase.type == 'list'">
                                <li v-for="el in phrase.value" :key="el">
                                    {{ el }}
                                </li>
                            </ul>

                            <div v-if="phrase.type == 'button'" class="buttonDiv">
                                <router-link :to="phrase.url" tag="button" class="buttonTeam">
                                    {{ phrase.value }}
                                    <i v-if="phrase.logo" :class="phrase.logo"></i>
                                </router-link>
                            </div>

                        </div>

                    </div>

                </div>

            </template>

        </maincontent>

    </div>
</template>

<style scoped lang="sass">

.topheader
    top: 0
    z-index: 999

#messagesWrapper

    display: flex
    flex-direction: column-reverse

    .message
        padding: 1em
        user-select: none

        h1
            color: $color-text-common
            text-align: center

        .buttonDiv
            margin-top: 1.5em
            margin-bottom: 1em

            .buttonTeam
                background-color: $color-orange
                color: white
                width: 100%
                height: 34px
                font-size: 0.9em
                border-radius: 30px
                position: relative

                &:hover
                    cursor: pointer

                i
                    position : absolute
                    right : 0
                    top : 0
                    line-height : 34px
                    width : 50px
                    text-align : center

</style>

<script>

import topheader from '@/components/assets/topheader/index.vue';
import { mapState } from 'vuex';
export default {
  name: 'teamroom',
  components: {

    topheader,

  },

  props : {
    
  },

  data : function(){
    return {
      messages: []
    }
  },

  computed:  mapState({
      pocketnet: state => state.pocketnet,
      minimized: state => state.minimized,
  }),

  methods : {

  },

  mounted() {
      this.messages = this.$t('teamMessages');
  }

}
</script>
